html,
body {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	font-family: 'Montserrat';
	font-weight: 400;
	font-size: 24px;
	color: #fff;
	background-image: url(../img/body_bg.png);
	background-repeat: no-repeat;
}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}

.main {
	flex: 1 0 auto;
}
.footer {
	flex: 0 0 auto;
}

._container{
  max-width: $maxWidthContainer + px;
  margin: 0 auto;
  padding: 0 15px;
}

._visually-hidden{
	position: absolute;
	width: 1px;
	height: 1px;
	border: 0;
	margin: -1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0 0 0 0);
}

.text-accent{
	color: $c-accent;
}