html {
  box-sizing: border-box;
}

*, *::after, *::before{
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul,
ol{
  padding: 0;
}

ul[class] {
  list-style: none;
}

img{
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a{
  text-decoration: none;
  color: inherit;
}

button{
  margin: 0;
  padding: 0;
}



